<template>
  <div id="app">
    <div>
      <!-- 动态加载表名 -->
      <select v-model="selectedTable" @change="fetchPoints">
        <option v-for="(table, index) in tables" :key="index" :value="table">{{ table }}</option>
      </select>
    </div>
    <div id="map"></div>
    <div v-if="selectedPoint" class="popup">
      <h3>点信息</h3>
      <p><strong>经度:</strong> {{ selectedPoint.longitude }}</p>
      <p><strong>纬度:</strong> {{ selectedPoint.latitude }}</p>
      <p><strong>可信度:</strong> {{ selectedPoint.Reliability }}</p> <!-- 使用 Level 替代 otherInfo -->
    </div>
    <!-- 弹出框内容 -->
    <div id="popup" class="popup">
      <div id="popup-content" ref="popupContent"></div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import 'ol/ol.css';  // 引入 OpenLayers 样式
import { Map, View } from 'ol';
import { Tile as TileLayer } from 'ol/layer';
import { OSM } from 'ol/source';
import { Point } from 'ol/geom';
import { Feature } from 'ol';
import { Vector as VectorLayer } from 'ol/layer';
import { Vector as VectorSource } from 'ol/source';
import { Style } from 'ol/style';
import { Overlay } from 'ol';
import { fromLonLat } from 'ol/proj';  // 引入坐标转换工具
import { Circle as CircleStyle } from 'ol/style';  // 导入 CircleStyle
import { Fill, Stroke } from 'ol/style';  // 导入 Fill 和 Stroke

export default {
  data() {
    return {
      tables: [],  // 用于存储数据库中的表名
      selectedTable: '',  // 当前选中的表名
      points: [],  // 当前选中表的点数据
      map: null,   // OpenLayers 地图实例
      selectedPoint: null,  // 当前选中的点的信息
      vectorSource: null,  // 用于存储标记的图层数据源
      vectorLayer: null,   // 用于存储标记的图层
      popup: null,         // 用于显示弹出框的覆盖层
    };
  },
  methods: {
    // 获取数据库中的所有表
    fetchTables() {
      axios.get('http://localhost:8081/api/tables')
          .then(response => {
            // 对表名进行排序并反转
            this.tables = response.data.sort().reverse();  // 排序并反转
            if (this.tables.length > 0) {
              this.selectedTable = this.tables[0];  // 默认加载第一个表的数据
              this.fetchPoints();  // 默认加载第一个表的数据
            }
          })
          .catch(error => {
            console.error('获取表名失败:', error);
          });
    },
    // 请求后端获取点数据
    fetchPoints() {
      if (!this.selectedTable) return;

      axios.get(`http://localhost:8081/api/points/${this.selectedTable}`)
          .then(response => {
            this.points = response.data;
            this.addMarkersToMap();  // 将点添加到地图
            this.updateMapView();  // 更新地图视图
          })
          .catch(error => {
            console.error('获取点数据失败:', error);
          });
    },
    // 将点数据添加到地图
    addMarkersToMap() {
      // 清除现有标记
      this.vectorSource.clear();

      // 添加新的标记
      this.points.forEach(point => {
        const marker = new Feature({
          geometry: new Point(fromLonLat([parseFloat(point.longitude), parseFloat(point.latitude)])), // 转换为地图坐标
          name: `Point at (${point.latitude}, ${point.longitude})`,
        });

        marker.setStyle(new Style({
          image: new CircleStyle({
            radius: 4,  // 圆圈的半径
            fill: new Fill({
              color: 'red',  // 圆圈的填充颜色
            }),
            stroke: new Stroke({
              color: 'darkred',  // 圆圈边框的颜色
              width: 1,  // 边框宽度
            }),
          }),
        }));

        this.vectorSource.addFeature(marker);
      });
    },

    // 更新地图的视图
    updateMapView() {
      if (this.points.length === 0) return;

      // 获取第一个点的经纬度
      const firstPoint = this.points[0];
      const coordinates = fromLonLat([parseFloat(firstPoint.longitude), parseFloat(firstPoint.latitude)]);

      // 设置地图中心为第一个点
      this.map.getView().setCenter(coordinates);

      // 设置地图的缩放级别（假设使用一个默认的缩放级别）
      this.map.getView().setZoom(6);  // 根据需要调整缩放级别
    },

    // 显示弹出框
    showPopup(point) {
      const coordinate = fromLonLat([parseFloat(point.longitude), parseFloat(point.latitude)]);  // 转换为地图坐标
      this.popup.setPosition(coordinate);  // 设置弹出框位置
      const content = `<div><strong>经度:</strong> ${point.longitude}<br><strong>纬度:</strong> ${point.latitude}<br><strong>可信度:</strong> ${point.Reliability}</div>`;
      this.$refs.popupContent.innerHTML = content;  // 更新弹出框内容
    }

  },
  mounted() {
    // 初始化地图
    this.vectorSource = new VectorSource();
    this.vectorLayer = new VectorLayer({
      source: this.vectorSource,
    });

    this.popup = new Overlay({
      element: document.getElementById('popup'),
      positioning: 'bottom-center',
      stopEvent: false,
    });

    this.map = new Map({
      target: 'map',
      layers: [
        new TileLayer({
          source: new OSM(),
        }),
        this.vectorLayer,
      ],
      view: new View({
        center: [0, 0],  // 初始中心坐标
        zoom: 3,  // 初始缩放级别
      }),
      overlays: [this.popup],
    });

    // 监听地图点击事件
    this.map.on('click', (event) => {
      // 获取点击的坐标
      const pixel = this.map.getEventPixel(event.originalEvent);
      const feature = this.map.forEachFeatureAtPixel(pixel, (feature) => {
        return feature;
      });

      if (feature) {
        // 获取对应的点数据
        const clickedCoordinate = feature.getGeometry().getCoordinates();
        const point = this.points.find(p => {
          const featureCoordinate = fromLonLat([parseFloat(p.longitude), parseFloat(p.latitude)]);
          return featureCoordinate[0] === clickedCoordinate[0] && featureCoordinate[1] === clickedCoordinate[1];
        });

        if (point) {
          this.selectedPoint = point;  // 设置当前选中的点信息
          this.showPopup(point);  // 显示弹出框
        }
      }
    });

    this.fetchTables();  // 加载表名
  },
};
</script>

<style>
html, body {
  height: 100%;
  margin: 0;
}

#app {
  height: 100%;
}

#map {
  width: 100%;
  height: 100%;
}

.popup {
  position: absolute;
  background: white;
  padding: 10px;
  border: 1px solid black;
  max-width: 200px;
  z-index: 200;
  white-space: nowrap;
}

.popup:after {
  display: none;
}
</style>
